import React, { Component } from 'react';
import '../styles/App.css';
import { Button, Navbar, Nav, NavDropdown, MenuItem, NavItem, Grid, Row, Col } from 'react-bootstrap'
import axios from 'axios';

export class Players extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      players: [],
    };
  }

//TODO: fix escaping of height, links to use react router dom

  componentDidMount() {
    axios.get('https://cors-anywhere.herokuapp.com/https://nfldb-backend.appspot.com/players', {
      crossdomain: true,
    })
    .then((response) => {
      console.log(response);
      this.setState(() => {
        return {
          players: response.data
        }
      });
    }).catch(function (error) {
        console.log(error);
    });
  }

  render() {
    return (
          <div class="container-fluid">
          <div class="row text-center">
            {this.state.players.map((player, index) => (
              <div class="col-lg-4">
               <div class="grid" key={player.id}>
                <a href={"players/" + player.id}>
                    <h3><i><b>{player.first_name + " " + player.last_name}</b></i></h3>
                    <img class="separator" src={player.pic_link} alt="" />
                    <div class="clearboth"></div>
                    <div>
                        <p class="alignleft"><b>Position:</b></p>
                        <p class="alignright"><b>{player.position}</b></p>
                    </div>
                    <div class="clearboth"></div>
                    <div>
                        <p class="alignleft"><b>Height:</b></p>
                        <p class="alignright"><b>{player.height}</b></p>
                    </div>
                    <div class="clearboth"></div>
                    <div>
                        <p class="alignleft"><b>Weight:</b></p>
                        <p class="alignright"><b>{player.weight}</b></p>
                    </div>
                    <div class="clearboth"></div>
                    <div>
                        <p class="alignleft"><b>College:</b></p>
                        <p class="alignright"><b>{player.high_school}</b></p>
                    </div>
                    <div class="clearboth"></div>
                </a>
            </div>
            </div>
            ))}
          </div>
          <footer class="footer">
          <p>&copy; Company 2017</p>
      </footer>
      </div>
  )};
}


