import React, { Component } from 'react';
import '../styles/App.css';
import { Button, Navbar, Nav, NavDropdown, MenuItem, NavItem, Grid, Row, Col } from 'react-bootstrap'
import axios from 'axios';

export class Coaches extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      coaches: []
    };
  }

  componentDidMount() {
    axios.get('https://cors-anywhere.herokuapp.com/https://nfldb-backend.appspot.com/coaches', {
      crossdomain: true,
    })
    .then((response) => {
      console.log(response);
      this.setState(() => {
        return {
          coaches: response.data
        }
      });
    }).catch(function (error) {
        console.log(error);
    });
  }

  render() {
    return (
      <div class="container-fluid">
          <div class="row text-center">
              {this.state.coaches.map((coach, index) => (
                <div class="col-lg-4" key={coach.id}>
                    <div class="grid">
                        <a href={"coaches/" + coach.id}>
                            <h3><i><b>{coach.first_name + " " + coach.last_name}</b></i></h3>
                            <img class="separator" src={coach.pic_link}/>
                            <div class="clearboth"></div>
                            <div>
                                <p class="alignleft"><b>Full Name:</b></p>
                                <p class="alignright"><b>{coach.first_name + " " + coach.last_name}</b></p>
                            </div>
                            <div class="clearboth"></div>
                            <div>
                                <p class="alignleft"><b>Position:</b></p>
                                <p class="alignright"><b>{coach.position}</b></p>
                            </div>
                            <div class="clearboth"></div>
                        </a>
                    </div>
                </div>
                ))}
          </div>
      <footer class="footer">
          <p>&copy; Company 2017</p>
      </footer>
      </div>
    );  
  }
}


