import React, { Component } from 'react';
import '../styles/App.css';
import { Button, Navbar, Nav, NavDropdown, MenuItem, NavItem, Grid, Row, Col } from 'react-bootstrap'
import axios from 'axios';

export class Teams extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      teams: []
    };
  }

  componentDidMount() {
    axios.get('https://cors-anywhere.herokuapp.com/https://nfldb-backend.appspot.com/teams', {
      crossdomain: true,
    })
    .then((response) => {
      console.log(response);
      this.setState(() => {
        return {
          teams: response.data
        }
      });
    }).catch(function (error) {
        console.log(error);
    });
  }

  render() {
    return (
       <div class="container-fluid">
          <div class="row text-center">
            {this.state.teams.map((team, index) => (
              <div class="col-lg-4">
                  <div class="grid">
                      <a href= {"teams/" + team.team_alias}>
                          <h3><i><b>{team.team_name}</b></i></h3>
                          <img class="separator" src={team.pic_link} alt="" />
                          <div class="clearboth"></div>
                          <div>
                              <p class="alignleft"><b>Stadium:</b></p>
                              <p class="alignright"><b>{team.venue_name}</b></p>
                          </div>
                          <div class="clearboth"></div>
                          <div>
                              <p class="alignleft"><b>Division:</b></p>
                              <p class="alignright"><b>{team.division}</b></p>
                          </div>
                          <div class="clearboth"></div>
                      </a>
                  </div>
              </div>
              ))}
          </div>
          <footer class="footer">
          <p>&copy; Company 2017</p>
      </footer>
      </div>
    );
  }

}


