import React, { Component } from 'react';
import '../styles/App.css';
import { Button, Navbar, Nav, NavDropdown, MenuItem, NavItem, Grid, Row, Col } from 'react-bootstrap'
import axios from 'axios';

export class Seasons extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      seasons: [],
    };
  }

  componentDidMount() {
    axios.get('https://cors-anywhere.herokuapp.com/https://nfldb-backend.appspot.com/seasons', {
      crossdomain: true,
    })
    .then((response) => {
      console.log(response);
      this.setState(() => {
        return {
          seasons: response.data
        }
      });
    }).catch(function (error) {
        console.log(error);
    });
  }

  render() {
    return (
     <div class="container-fluid">
          <div class="row text-center">
            {this.state.seasons.map((season, index) => (
              <div class="col-lg-4" key={season.id}>
                  <div class="grid">
                      <a href={"seasons/" + season.id}>
                        <h3><i><b>2016</b></i></h3>
                        <img src={season.pic_link} class="separator" alt="..."></img>

                        <div class="clearboth"></div>
                        <div>
                            <p class="alignleft"><b>Super Bowl Champ:</b></p>
                            <p class="alignright"><b>{season.super_bowl_champion}</b></p>
                        </div>
                        <div class="clearboth"></div>
                        <div>
                            <p class="alignleft"><b>Super Bowl MVP:</b></p>
                            <p class="alignright"><b>{season.super_bowl_mvp}</b></p>
                        </div>
                        <div class="clearboth"></div>
                        <div>
                            <p class="alignleft"><b>Season MVP:</b></p>
                            <p class="alignright"><b>{season.season_mvp}</b></p>
                        </div>
                        <div class="clearboth"></div>
                      </a>
                  </div>
              </div>
            ))}
          </div>
          <footer class="footer">
            <p>&copy; Company 2017</p>
          </footer>
        </div>
    )};
}


