import React, { Component } from 'react';
import '../styles/App.css';
import { Button, Navbar, Nav, NavDropdown, MenuItem, NavItem, Grid, Row, Col } from 'react-bootstrap'
import axios from 'axios';

export class Season extends React.Component {
	
	constructor(props) {
		super(props);

		this.state = { 
			nfc_champion: null,
      year: null,
      season_mvp: null,
      super_bowl_mvp: null,
      start_date: null,
      afc_champion: null,
      pic_link: null,
      super_bowl_champion: null,
      end_date: null,
		};
	}

	componentDidMount() {
		axios.get('https://cors-anywhere.herokuapp.com/https://nfldb-backend.appspot.com/seasons/' + this.props.match.params.id, {
      crossdomain: true,
    })
    .then((response) => {
      console.log(response);
      this.setState(() => {
        return {
          nfc_champion: response.data.nfc_champion,
		      year: response.data.year,
		      season_mvp: response.data.season_mvp,
		      super_bowl_mvp: response.data.super_bowl_mvp,
		      start_date: response.data.start_date,
		      afc_champion: response.data.afc_champion,
		      pic_link: response.data.pic_link,
		      super_bowl_champion: response.data.super_bowl_champion,
		      end_date: response.data.end_date,
        }
      });
    }).catch(function (error) {
        console.log(error);
    });
	}

	render() {
		let seasonTitle = this.state.year + " Season";
		let duration = this.state.start_date + " to " + this.state.end_date;

		return (
			<div>
				<div class="container-fluid">
					<div class="row text-left">
						<div class="col-lg-2"></div>
						<div class="col-lg-10">
							<h1 class="title"><b>{ seasonTitle }</b><small> { duration }</small></h1>
						</div>
					</div>
				</div>
				<br></br>

				<div class="container-fluid">
					<div class="row text-center">
						<div class="col-lg-2"></div>
						<div class="col-lg-4">
								<div class="grid">
										<img src={ this.state.pic_link } alt="" />
								</div>
						</div>
						<div class="col-lg-4">
							<div>
								<h2 class="instanceinfo" align="left">Season Results</h2>
								<hr></hr>
								<div>
									<p class="alignleft clearboth"><b>AFC Champion:</b></p>
									<p class="alignright"><b>{ this.state.afc_champion }</b></p>
								</div>
								<br></br>
								<div>
									<p class="alignleft clearboth"><b>NFC Champion:</b></p>
									<p class="alignright"><b>{ this.state.nfc_champion }</b></p>
								</div>
								<br></br>
								<div>
									<p class="alignleft clearboth"><b>Super Bowl Champion:</b></p>
									<p class="alignright"><b>{ this.state.super_bowl_champion }</b></p>
								</div>
								<br></br>
							</div>
						</div>
						<div class="col-lg-2"></div>
					</div>
				</div>

				<div class="container-fluid">
					<div class="row text-center">
						<div class="col-lg-2"></div>
						<div class="col-lg-5">
							<div>
								<h2 class="instanceinfo" align="left">Most Valuable Players</h2>
								<hr></hr>
								<div>
									<p class="alignleft clearboth"><b>Season MVP:</b></p>
									<p class="alignright"><b>{ this.state.season_mvp }</b></p>
								</div>
								<br></br>
								<div>
									<p class="alignleft clearboth"><b>Super Bowl MVP:</b></p>
									<p class="alignright"><b>{ this.state.super_bowl_mvp }</b></p>
								</div>
								<br></br>
							</div>
						</div>
						<div class="col-lg-5"></div>
					</div>
				</div>

				<footer class="footer">
					<div class="col-lg-2"></div>
					<div class="col-lg-5">
						<p>&copy; Company 2017</p>
					</div>
					<div class="col-lg-5"></div>
				</footer>
			</div>
		);
	}
}


